@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bitter&display=swap);
/* Global Styles */
:root {
   --primary-color: #102a4b;
   --secondary-color: #02558b;
   --dark-color: #333333;
   --light-color: #33a3dc;
   --danger-color: #f8961d;
   --success-color: #3cb666;
}

* {
   box-sizing: border-box;
   margin: 0;
   padding: 0;
}

