.login-background {
   background: url(../../static/media/LoginImage.9ce2984e.jpg) no-repeat center center fixed;
   background-size: cover;
   height: 100vh;
   overflow: hidden;
}

body {
   font-family: "Bitter", serif;
   font-weight: 400;
   font-style: normal;
   font-size: 1rem;
   line-height: 1.6;
   background-color: #fff;
   color: #333;
}

h1 {
   font-family: "Poppins:wght@700", sans-serif;
}

h2 {
   font-family: "Poppins:wght@600", sans-serif;
}

a {
   color: #102a4b;
   color: var(--primary-color);
   text-decoration: none;
   padding: 0 0.3rem;
}

a:hover {
   color: #33a3dc;
   color: var(--light-color);
}

ul {
   list-style: none;
}

img {
   width: 100%;
}

.logo {
   width: 30px;
   height: 30px;
}

/* Utilities */
.container {
   max-width: 1100px;
   margin: auto;
   overflow: hidden;
   padding: 0 2rem;
   box-shadow: 0.1rem 0.2rem 0.5rem;
}

.container-tall {
   max-width: 1100px;
   margin: auto;
   overflow: hidden;
   padding: 2rem;
}

.chapter a {
   color: whitesmoke;
}
.chapter a:hover {
   color: #33a3dc;
   color: var(--light-color);
}

.transparent {
   background: rgba(255, 255, 255, 0.8);
}

/* Text Styles*/
.x-large {
   font-size: 4rem;
   line-height: 1.2;
   margin-bottom: 1rem;
}

.large {
   font-size: 3rem;
   line-height: 1.2;
   margin-bottom: 1rem;
}

.lead {
   font-size: 1.5rem;
   margin-bottom: 1rem;
}

.text-center {
   text-align: center;
}

.text-primary {
   color: #102a4b;
   color: var(--primary-color);
}

.text-secondary {
   color: #02558b;
   color: var(--secondary-color);
}

.text-dark {
   color: #333333;
   color: var(--dark-color);
}

.text-danger {
   color: #f8961d;
   color: var(--danger-color);
}

.text-white {
   color: whitesmoke;
}

.text-success {
   color: #3cb666;
   color: var(--success-color);
}

.text-center {
   text-align: center;
}

.text-right {
   text-align: right;
}

.text-left {
   text-align: left;
}

sup {
   font-size: medium;
}

/* Cards */
.card {
   padding: 1rem 0rem;
   border: #ccc 1px solid;
   box-shadow: 0.2rem 0.2rem 0.5rem;
   margin: 0.7rem 0;
}

.shadow-box {
   box-shadow: 0.2rem 0.2rem 0.5rem black;
}

/* List */
.list {
   margin: 0.5rem 0;
}

.list li {
   padding-bottom: 0.3rem;
}

/* Padding */
.p {
   padding: 0.5rem;
}
.p-1 {
   padding: 1rem;
}
.p-2 {
   padding: 2rem;
}
.p-3 {
   padding: 3rem;
}
.p-4 {
   padding-top: 8rem;
}
.no-p {
   padding: 0;
}
.py {
   padding: 0.5rem 0;
}
.py-1 {
   padding: 1rem 0;
}
.py-2 {
   padding: 2rem 0;
}
.py-3 {
   padding: 3rem 0;
}
.p-title {
   padding: 0 4rem;
}
.p-page {
   padding: 4rem 6rem;
}
.drugnames {
   padding: 0 0.75rem;
}
/* Margin */
.no-m {
   margin: 0;
}
.m {
   margin: 0.5rem;
}
.m-1 {
   margin: 1rem;
}
.m-top-bottom-1 {
   margin: 1rem 0rem;
}
.left-m-1 {
   margin: 1rem 0rem 1rem 1rem;
}
.right-m-1 {
   margin: 1rem 1rem 1rem 0rem;
}
.m-2 {
   margin: 2rem;
}
.m-3 {
   margin: 3rem;
}
.my {
   margin: 0.5rem 0;
}
.my-1 {
   margin: 1rem 0;
}
.my-2 {
   margin: 2rem 0;
}
.my-3 {
   margin: 3rem 0;
}
.space-right-1 {
   margin: 0 1rem 0 0;
}
.space-right-2 {
   margin: 0 2rem 0 0;
}

/* Grid */
.grid-2 {
   display: grid;
   grid-template-columns: repeat(2, 1fr);
   grid-row-gap: 1rem;
   grid-column-gap: 2rem;
}

.grid-3 {
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   grid-gap: 1rem;
}

.grid-4 {
   display: grid;
   grid-template-columns: repeat(4, 1fr);
   grid-gap: 2rem;
}
.grid-5 {
   display: grid;
   grid-template-columns: repeat(5, 1fr);
   grid-gap: 1rem;
}

.grid-2-3 {
   display: grid;
   grid-template-columns: 2fr 3fr;
   grid-gap: 2rem;
}

.no-gap {
   grid-gap: 0rem;
}

.btn {
   display: inline-block;
   background: #33a3dc;
   background: var(--light-color);
   color: #333;
   padding: 0.4rem 1.3rem;
   font-size: 1rem;
   border: none;
   cursor: pointer;
   margin-right: 0.5rem;
   transition: opacity 0.2s ease-in;
   outline: none;
}
.btn:disabled {
   background-color: gray;
}

.btn-link {
   background: none;
   padding: 0;
   margin: 0;
}

.btn-block {
   display: block;
   width: 100%;
}

.btn-sm {
   font-size: 0.8rem;
   padding: 0.3rem 1rem;
   margin-right: 0.2rem;
}

.badge {
   font-size: 0.8rem;
   padding: 0.2rem 0.7rem;
   text-align: center;
   margin: 0.3rem;
   background: #33a3dc;
   background: var(--light-color);
   color: #333;
   border-radius: 5px;
}

.alert {
   padding: 0.7rem;
   margin: 1rem 0;
   opacity: 0.9;
   background: #33a3dc;
   background: var(--light-color);
   color: #333;
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
   background: #102a4b;
   background: var(--primary-color);
   color: #fff;
}

.btn-secondary,
.bg-secondary,
.badge-secondary,
.alert-secondary {
   background: #02558b;
   background: var(--secondary-color);
   color: #fff;
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
   background: #33a3dc;
   background: var(--light-color);
   color: #333;
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
   background: #333333;
   background: var(--dark-color);
   color: #fff;
}

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
   background: #f8961d;
   background: var(--danger-color);
   color: #fff;
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
   background: #3cb666;
   background: var(--success-color);
   color: #fff;
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
   background: #fff;
   color: #333;
   border: #ccc solid 1px;
}

.bg-grey {
   background: lightgray;
}

.btn:hover {
   opacity: 0.8;
}

.bg-light,
.badge-light {
   border: #ccc solid 1px;
}

.round-img {
   border-radius: 50%;
}

/* Forms */
input {
   margin: 1.2rem 0;
}

.form-text {
   display: block;
   margin-top: 0.3rem;
   color: #888;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="date"],
input[type="number"],
select,
textarea {
   display: block;
   width: 100%;
   padding: 0.4rem;
   font-size: 1.2rem;
   border: 1px solid #ccc;
}

input[type="submit"],
button {
   font: inherit;
}

.form-group {
   margin: 1.2rem 0;
}

.form-group input {
   margin: 0.2rem 0;
}

.form-container {
   max-width: 500px;
   margin: auto;
   overflow: hidden;
   padding: 0 2rem;
   box-shadow: 0.2rem 0.2rem 0.5rem;
   border: 1px solid lightgray;
}

.form-container h1,
h2,
h3 {
   text-align: center;
}

table th,
table td {
   padding: 1rem;
   text-align: left;
}

table th {
   background: #33a3dc;
   background: var(--light-color);
}

/* Navbar */
.navbar {
   display: flex;
   /* justify-content: space-between; */
   align-items: center;
   padding: 0.7rem 2rem;
   z-index: 1;
   width: 100%;
   flex-wrap: wrap;
}

.title {
   width: 100%;
   flex-wrap: nowrap;
}

.navbar ul {
   display: flex;
   flex: 1 1;
}

.navbar a {
   color: #fff;
   padding: 0.45rem;
   margin: 0 0.25rem;
}

.navbar a:hover {
   color: #33a3dc;
   color: var(--light-color);
}

.navbar .welcome span {
   margin-right: 0.6rem;
}

/* Animation (Add After) */
.item-enter {
   opacity: 0;
}
.item-enter-active {
   opacity: 1;
   transition: opacity 1000ms ease-in;
}
.item-exit {
   opacity: 1;
}
.item-exit-active {
   opacity: 0;
   transition: opacity 1000ms ease-in;
}

/* Mobile Styles */
@media (max-width: 700px) {
   .hide-sm {
      display: none;
   }

   .grid-2,
   .grid-3,
   .grid-4 {
      grid-template-columns: 1fr;
      grid-gap: 1rem;
   }
   .p-title {
      padding: 0 1rem;
   }
   .p-page {
      padding: 1rem;
   }
   a {
      padding: 0.25rem 2rem;
   }
   li {
      margin: 1rem 0;
   }

   /* Text Styles */
   .x-large {
      font-size: 3rem;
   }

   .large {
      font-size: 2rem;
   }

   .lead {
      font-size: 1rem;
   }

   /* Navbar */
   .navbar {
      display: block;
      text-align: center;
   }

   .navbar h1 {
      margin-bottom: 0.6rem;
   }

   .navbar ul {
      display: flex;
      flex-wrap: wrap;
      text-align: center;
      justify-content: center;
   }
}

/* yad css */
.yadcard {
   display: flex;
   justify-content: center;
   align-items: center;
   position: relative;
   border-radius: 0.25rem;
   box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.3);
   background-color: white;
   height: 160px;
   width: 160px;
   transition: 980ms;
   cursor: pointer;
   transform: perspective(1000px) rotateY(0)
      translateY(0);
   transform: perspective(1000px) rotateY(var(--rotate-y, 0))
      translateY(var(--translate-y, 0));
   transform-style: preserve-3d;
}
.yadcard.font {
   left: 0;
}

.yadcard:hover {
   box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.5);
   --translate-y: -4px;
}

.yadcard.flip {
   --rotate-y: 180deg;
}

.yadcard .front,
.yadcard .back {
   position: absolute;
   padding: 1rem;
   -webkit-backface-visibility: hidden;
           backface-visibility: hidden;
}

.yadcard .back {
   transform: rotateY(180deg);
}

.flashcard-options {
   margin-top: 0.5rem;
}

.yadcontainer {
   max-width: 900px;
   margin: 1rem 2rem;
}

.flashcard-option {
   margin-top: 0.25rem;
   color: #555;
   font-size: 0.9rem;
}

.card-grid {
   grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
   grid-gap: 1rem;
   gap: 1rem;
}

.pagination {
   display: flex;
   align-items: center;
}
.pagination input {
   width: 100px;
   margin-left: 7px;
   margin-right: 10px;
}
.pagination-btn {
   border: 1px solid gray;
   height: 40px;
   width: 40px;
   display: flex;
   align-items: center;
   justify-content: center;
   border-radius: 5px;
   box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
   cursor: pointer;
}

#admin-page .btn {
   background-color: #48a3f7;
   color: white;
}
#admin-page th {
   background-color: #48a3f7 !important;
   color: white;
}

